						src: process.env.PUBLIC_URL + '/img/ceo2.JPG',
						thumbnail: process.env.PUBLIC_URL + '/img/ceo2.JPG',
						thumbnailWidth: 320,
						thumbnailHeight: 212,
						caption: "Boats (Jeshu John - designerspics.com)"
